import React, { useState } from 'react';
import styles from './Transfer.module.scss';
import info from '../../../img/UI/info.svg';
import InputField from '../../../components/InputField/InputField';
import LinkBack from '../../../components/LinkBack/LinkBack';
import { TRANSFERS_ROUTE } from '../../../utils/consts';
import Success from './../../../components/Success/Success';
import { send } from './../../../API/transactionsAPI';
import userStore from '../../../store/UserStore';
import { checkIfNumberIsMoney, validateMoney } from './../../../utils/validate';
import Alert from './../../../components/Alert/Alert';

const Send = () => {
  const [isShow, setIsShow] = useState(false);
  const [money, setMoney] = useState('');
  const [email, setEmail] = useState('');
  const [commentary, setCommentary] = useState('');
  const [isConfirm, setIsConfirm] = useState(false);
  const [showAlert, setShowAlert] = useState({
    show: false,
    message: ''
  });
  const sendMoney = async (e) => {
    e.preventDefault();
    try { 
      const data = await send(email, money);
      userStore.setBalance(data.balance);
      setIsShow(true);
    } catch(e) {
      setShowAlert({show: true, message: e.response.data.detail});
      setTimeout(() => setShowAlert({show: false, message: e.response.data.detail}), 2000);
    }
  }
  return (
    <>
      {isConfirm 
      ?     
        <>    
          <Success isShow={isShow} setShow={setIsShow} 
            title='You’ve sent' 
            message={`${money}$ to ${email}`}
            btn='Transfer money again'/>
          <LinkBack title='Confirm transactions'  onClick={() => setIsConfirm(false)}/>
          <div className={styles.transfer}>
            <div className={styles.transfer__header}>
              <h1 className={styles.transfer__title}>Confirm transactions</h1>
            </div>
            <div className={styles.transfer__contents}>
              <div className={styles.transfer__content}>
                <div className={styles.content__title}>Recipient</div>
                <div className={styles.content__text}>{email}</div>
              </div>
              <div className={styles.transfer__content}>
                <div className={styles.content__title}>Amount</div>
                <div className={styles.content__text}>{`$${money}`}</div>
              </div>
              <div className={styles.transfer__content + " " + styles.transfer__content_next}>
                <div className={styles.content__title}>Email</div>
                <div className={styles.content__text}>{email}</div>
              </div>
              {commentary &&
                <div className={styles.transfer__content}>
                  <div className={styles.content__title}>Commentary</div>
                  <div className={styles.content__text}>{commentary}</div>
                </div>
              }
            </div>
            <div className={styles.transfer__btns}>
              <button className={`btn ${styles.transfer__btn}`} onClick={sendMoney}>Confirm</button>
              <button className={`btn ${styles.transfer__btn} ${styles.transfer__btn_cancel}`} onClick={() => setIsConfirm(false)}>Back</button>
            </div>
            {showAlert.show &&
              <Alert message={showAlert.message}/>
            }
          </div>
        </>
      :
        <>
          <LinkBack title='Between my accounts' path={TRANSFERS_ROUTE}/>
          <div className={styles.transfer}>
            <div className={styles.transfer__header}>
              <h1 className={styles.transfer__title}>Send money</h1>
              <img src={info} alt="info" />
            </div>
            <p className={styles.transfer__secondTitle}>US and foregein banks</p>
            <div className={styles.transfer__field}>
              <p className={styles.transfer__subtitle}>To</p>
              <InputField placeholder='Email' inputType='email' titleName='Enter email' value={email}
                onChange={(e) => setEmail(e.target.value)}
                field={styles.transfer__input} title={styles.transfer__inputTitle}
                required/>
            </div>
            <div className={styles.transfer__field}>
              <p className={styles.transfer__subtitle}>Amount</p>
              <InputField placeholder='0.00$' inputType='number' titleName='Enter the amount' value={money}
                onChange={(e) => {
                  validateMoney(e.target);
                  setMoney(e.target.value);
                }}
                field={styles.transfer__input} title={styles.transfer__inputTitle}
                required/>
              <div className={styles.info}>
                <img src={info} alt="info" />
                <p className={styles.info__title}>When transferring more than 15 000.00$, you must indicate recipient’s name</p>
              </div>
            </div>
            <div className={styles.transfer__field}>
              <InputField placeholder='Add a commentar' inputType='text' titleName='Commentary' value={commentary}
                onChange={(e) => setCommentary(e.target.value)}
                field={styles.transfer__input} title={styles.transfer__inputTitle}/>
            </div>
            <div className={styles.transfer__btns}>
              <button disabled={money === '' || email === '' || !checkIfNumberIsMoney(money) ? true : false} className={`btn ${styles.transfer__btn}`} onClick={() => setIsConfirm(true)}>Continue</button>
              <button className={`btn ${styles.transfer__btn} ${styles.transfer__btn_cancel}`}>Cancel</button>
            </div>
          </div>
        </>  
      }
    </>
  );
};

export default Send;